.row__posters {
    display: flex;
   

    overflow-x: scroll;
    overflow-y: hidden;
    padding: 20px;
}

.row__posters::-webkit-scrollbar {
    display: none;
}

.row__poster {
    transition: transform 0.2s;
     width: 100%;
    max-height: 100px;
    object-fit: contain;
    margin-right: 10px;
}

.row__poster:hover {
    transform: scale(1.08);
}

.row__posterLarge {
    max-height: 250px;
}

.row__posterLarge:hover {
    transform: scale(1.09);
}

.row {
    margin-left: 20px;
    color: white;
}
