.footer-outer-container {
  background-color: #111;
  color: grey;
  display: flex;
  justify-content: center;
}
.footer-inner-container {
  width: 60%;
}
.footer-icons {
  width: 15%;
  display: flex;
  justify-content: space-between;
  /* margin-left: 10px; */
  color: #fff;
}
.footer-data {
  display: flex;
  justify-content: space-between;
}
.footer-data ul {
  list-style: none;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  height: 12vh;
  /* background-color: blue; */
}
.footer-data ul li {
  cursor: pointer;
}
.service-code {
  display: flex;
  align-items: center;
  justify-content: center;
  border: #fff 1px solid;
  width: 15%;
  margin: 10px 0;
  cursor: pointer;
}
.copy-write {
  font-size: 12px;
  margin-bottom: 10px;
}
@media (max-width: 1000px) {
  .footer-inner-container {
    width: 80%;
  }
}
@media (max-width: 900px) {
  .footer-inner-container {
    width: 75%;
  }
  .footer-icons {
    width: 16%;
  }
  .service-code {
    width: 16%;
  }
}
@media (max-width: 768px) {
.footer-inner-container{
    width: 95%;
}
}

